  logout: (params,headers) => post('/api/auth/logout',params,headers),// 
  devicesList: (params,headers) => post('/api/host/list',params,headers),// 
  getRoleList: (params,headers) => post('/api/host/role/list',params,headers),// 
  devicesSave: (params,headers) => post('/api/host/save',params,headers),// 
  getDevicesInfo: (params,headers) => post('/api/host/info',params,headers),// 
  devicesDel: (params,headers) => post('/api/host/del',params,headers),// 
  getEnableServices: (params,headers) => post('/api/host/list/enable',params,headers),// 
  getContactList: (params,headers) => post('/api/contact/list',params,headers),// 
  contactSave: (params,headers) => post('/api/contact/save',params,headers),// 
  deleteCustom: (params,headers) => post('/api/contact/del',params,headers),// 
  getContactInfo: (params,headers) => post('/api/contact/info',params,headers),// 
  getContactListEnable: (params,headers) => post('/api/contact/list/enable',params,headers),// 
  getSettingList: (params,headers) => post('/api/web/setting/list',params,headers),// 
  paramsSave: (params,headers) => post('/api/web/setting/save',params,headers),// 
  getNoticeList: (params,headers) => post('/api/notification/setting/list',params,headers),// 
